.blog{
    background-color: lighten($color-primary, 18%);
    padding-bottom: 60px;
}
.blog__inner {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px;
}
.blog__item {
    width: 33.33%;
    padding: 0 15px;
    display: flex;
}
.blog-card {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    margin-bottom: 25px;
}

.blog-card__body {
    padding: 15px 5px 15px 20px;
}
.blog-card__img {
    display: block;
    img {
        width: 100%;
        object-fit: cover;
    }
}
.blog-card__category {
    font-style: italic;
    font-weight: 700;
    letter-spacing: 0.03em;
    margin-bottom: 20px;
}
.blog-card__title {
    font-size: 18px;
    letter-spacing: 0.05em;
    font-weight: 500;
    line-height: 1.5em;
    text-transform: uppercase;
}
