.reviews{
    padding-bottom: 40px;
}
.reviews__inner {
    position: relative;
    padding-bottom: 20px;
}
.reviews-slider{
    .slider-reviews__pagination {
        bottom: 0;
    }
    .reviews-slider__nav {
        width: 100px;
        position: absolute;
        right: 100px;
        top: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
        color: $color-primary;
    }
    .slider-products__pagination {
        width: 100%;
        bottom: 0;
    }
    .swiper-pagination-bullet-active {
        background-color: $color-primary;
    }
}
.review {
    max-width: 700px;
    margin: 0 auto;
    font-style: italic;
    &__name{
        font-size: 30px;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        font-weight: 700;
        margin-bottom: 20px;
    }
    &__text{
        font-size: 18px;
        line-height: 1.7em;
        margin-bottom: 10px;
    }
    &__date{
        font-weight: 700;
    }
}