.services{
    padding-bottom: 60px;
}
.services__inner{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px 25px -20px;
}
.service{
   width: 33.33%;
   padding: 20px;
   margin-bottom: 20px;
}

.card{
    width: 100%;
    height: 450px;
    position: relative;
    transform-style: preserve-3d;
    transition: 1s;
    background-color: rgb(173, 69, 69);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    @include backgound;
    &::after{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(10.58deg, #F383B5 10.09%, rgba(243, 131, 181, 0) 39.51%);
        border-radius: 20px
    }
}
.card-title {
    @include font ($font-primary,25,500);
    text-transform: uppercase;
    transform: translateZ(40px);
    letter-spacing: 0.07em;
    color: #fff;
    margin-bottom: 40px;
}

.card-body{
    display: flex;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, -40px) rotateY(180deg);
    width: 85%;
    height: 87%;
    border-radius: 20px;
    overflow: hidden;
    border: 5px solid #fff;
}

.card-content {
    display: flex;
    width: 100%;
}

.card-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.card-price{
    display: flex;
    justify-content: space-around;
    align-items: center;
    &__title{
        &::first-letter{
            text-transform: uppercase;
        }
    }
}

.card:hover{
    transform: rotateY(180deg);
}

.card:hover .card-content{
    opacity: 1;
    transition-delay: 1.5s;
}

.services__button {
    text-align: center;
}