.top {
    min-height: 610px;
    // background-image: url('../images/bg/bg-top.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    // color: #fff;
}


.top-content{
    max-width: 570px;
    padding-top: 170px;
    margin-left: 100px;
    color: #fff;
    &__title{
        // @include font ($font-primary,70,500);
        font-size: 70px;
        text-transform: uppercase;
        line-height: 1.2em;
        margin-bottom: 20px;
        span {
            position: relative;
            &::after {
                content: '';
                position: absolute;
                width: 100%;
                height: 3px;
                background-color: #fff;
                left: 0;
                bottom: -10px;
            }
        }
    }
    &__text{
        margin-bottom: 20px;
        padding-right: 20px;
    }
    // &__btn{}
}
