.about{
    background-color: lighten($color-primary, 18%);
    padding-bottom: 60px;
}

.about__inner{
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.about__content{
    max-width: 500px;
    &-title{
        margin-bottom: 20px;
    }
}
.about__text{
    font-style: italic;
    font-size: 18px;
    line-height: 1.5em;
    margin-bottom: 20px;
}
.about__photo{
    max-width: 500px;
    img {
        border-radius: 8px;
    }
}