
html {
    box-sizing: border-box;
}
*, *::after, *::before {
    box-sizing: inherit;
}

body {
    @include font ($font-primary,16,400);
    min-width: 320px;
    overflow-x: hidden;
    line-height: 1.5em;
    color: $color-accent;
}
.container {
    max-width: 1230px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}
h3 {
    font-size: 25px;
    text-transform: uppercase;
    font-weight: 500;
}   
img {
    display: block;
    max-width: 100%;
    height: auto;
}
a {
    display: inline-block;
    color: inherit;
    text-decoration: none;
}
.btn-second {
    background-color: $color-secondary;
    color: #ffffff;
    font-style: italic;
    font-size: 14px;
    text-align: center;
    padding: 10px 23px;
    border-radius: 8px;
    border: 0px;
    transition: all 0.3s;
    cursor: pointer;
    &:hover {
        background-color: darken($color-secondary, 10%);
        transition: all 0.3s;
    }
}
.btn-main{
    color: #ffffff;
    font-style: italic;
    font-size: 14px;
    text-align: center;
    background-color: $color-accent;
    padding: 8px 15px;
    border-radius: 8px;
    border: 0px;
    transition: all 0.5s;
    text-transform: uppercase;
    letter-spacing: 0.03em;
    &:hover {
        background-color: darken($color-accent, 10%);
        transition: all 0.3s;
    }
}
.title {
    @include font ($font-primary,45,400);
    line-height: 1.2em;
    color: $color-accent;
    text-align: center;
    text-transform: uppercase;
    padding: 80px 0;
    span {
        position: relative;
        &::after,
        &::before {
            content: '';
            position: absolute;
            width: 25%;
            height: 2px;
            background-color: $color-accent;
        }
        &::after {
            left: 0;
            top: -5px;
        }
        &::before {
            bottom: -5px;
            right: 0; 
        }
    }
}

.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    background-color:  lighten($color-primary, 15%);
    display: flex;
    justify-content: center;
    // align-items: center;
    padding-top: 100px;
    opacity: 0;
    z-index: -1;
    transition: all 1s ease-in;
    .form{
        width: 100%;
    }
    .form__control{
        margin-bottom: 15px;
    }
    .form__input,
    .form__text {
        width: 100%;
        padding: 10px;
        border-radius: 8px;
        outline: none;
        border: 0px;
    }
        .form__text{
            min-height: 100px;
        }
        .form__btn{
            width: 50%;
        }
    }
.modal.modal-open {
    opacity: 1;
    z-index: 100;
}
.modal-close {
    font-size: 55px;
    position: absolute;
    top: -40px;
    right: 0;
    cursor: pointer;
}
.modal__content {
    position: relative;
    width: 50%;
    transform: translateY(-100%);
    transition: transform 0.5s ease-in;
}
.modal-open .modal__content {
    transform: translateY(50px);
    transition: transform 1s ease-in;
}
