.portfolio{
    padding-bottom: 60px;
}

.portfolio__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 250px);
    grid-gap: 20px;
    color: #fff;
}
.portfolio__item {
    background-color: $color-secondary;
    @include backgound;
    // background-position: center;
    border-radius: 10px;

}
.portfolio__item--big {
    grid-column: span 2;
    grid-row: span 2;
}