.offers{
    background-color: lighten($color-primary, 18%);
    padding-bottom: 60px;  
}
.offers__inner {
    display: flex;
    min-height: 500px;
    flex-wrap: wrap;

}

.offers__item {
    @include backgound;
    width: 50%;
    display: flex;
}
.offer {
    width: 400px;
    min-height: 500px;
    background-color: $color-secondary;
    text-align: center;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;    
    padding: 20px 20px;
}
.offer__title {
    font-weight: 500;
    @include font ($font-primary,30,400);
    letter-spacing: 0.03em;
    margin-bottom: 20px;
}
.offer__text{
    line-height: 1.8em;
    font-size: 18px;
   padding: 0 10px 20px 10px;
}
.offer__discount {
    @include font ($font-accent,140,400);
    padding-top: 20px;
    padding-bottom: 20px;
}