.advantages{
    background-color: lighten($color-primary, 18%);
    padding-bottom: 60px;
}
.advantages-list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -10px;
    &__item {
        text-align: center;
        padding: 0 10px;
        margin-bottom: 20px;
        width: 25%;
    }
}
.advantag{
    background-color: #fff;
    padding: 20px 5px;
    box-shadow: 1px 1px 18px 0px lighten($color-secondary, 35%);;


    
    &__icon{
        font-size: 40px;
        margin-bottom: 15px;
    }
    &__title{
        font-size: 20px;
        line-height: 1.5em;
        &::first-letter{
            text-transform: uppercase;
        }
        span {
            display: block;
        }
    }
}