.header {
    padding: 5px 0;
    background-color: $color-accent;
    color: #fff;
    position: fixed;
    width: 100%;
    z-index: 2;
}
.header__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.menu{
    display: flex;
    font-size: 15px;
    // font-weight: 500;
    text-transform: uppercase;
    &__item{
        margin-right: 35px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link{
        position: relative;
        padding-bottom: 2px;
        transition: all 0.3s;
        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #fff;
            left: 0;
            bottom: 0;
            transform: scale(0);
            transition: all 0.3s;
        }
    }
    &__link:hover {
        &::after {
            transform: scale(1);
            transition: all 0.3s;
        }
    }
}
.social{
    display: flex;
    align-items: center;
    &__item{
        width: 30px;
        height: 30px;
        border: 1px solid #fff;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        transition: all 0.3s;
        &:last-child {
            margin-right: 0;
        }
    }
    &__item:hover {
        background-color: $color-secondary;
        border: 1px solid $color-secondary;
        transition: all 0.3s;
    }
}
.burger {
    display: none;
    width: 35px;
    &__item {
        width: 100%;
        height: 3px;
        background-color: #fff;
        margin-bottom: 7px;
        transition: all .5s ease;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
.burger.toggle .burger__item:nth-child(1) {
    transform: translateY(12px) rotate(45deg);
    transition: all .5s ease;
}
.burger.toggle .burger__item:nth-child(2) {
    opacity: 0;
}
.burger.toggle .burger__item:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
    transition: all .5s ease;
}
.nav__btn {
    display: none;
}
.nav__social{
    display: none;
}