@media only screen and (max-width:992px){
    body.lock {
        overflow: hidden;
    }
    .nav {
        opacity: 0;
        position: absolute;
        visibility: hidden;
        transition: all 0.5s ease;
        transform: translateX(100%);
        left: 0;
        right: 0;
        top: 75px;
        width: 100%;
        background-color: darken($color-accent, 10%);
        .menu {
            flex-direction: column;
            align-items: center;
            padding: 15px 10px;
        }
        .menu__item {
            margin-right: 0;
            padding: 15px;
        }
    }
    nav.show {
        opacity: 1;
        visibility: visible;
        position: absolute;
        transform: translateX(0%);
        transition: all 0.5s ease;
    }
    .offers__item {
        width: 100%;
        &--alignment {
            justify-content: flex-end
        }
    }
    .offer {
       width: 50%; 
    }
    .burger {
        display: block;
    }
    .advantages-list__item {
        width: 50%;
    }
    .services__inner  {
        justify-content: center;
    } 
    .service {
        width: 50%;
    }
    .reviews-slider {
        .swiper-button-prev::after,
        .swiper-button-next::after {
            font-size: 30px;
        }
    }
    .about__content {
        max-width: 100%;
        margin-bottom: 50px;
        text-align: center;
    }
    .about__photo {
        max-width: 100%;
    }
    .blog__item {
        width: 50%;
    }
}

@media only screen and (max-width:700px) {
    .title {
        font-size: 30px;
        padding: 50px 0;
    }
    .top-content {
        padding-top: 200px;
    }
    .top-content__title {
        font-size: 50px;
    }
    .service {
        width: 70%;
    }
    .card {
        height: 300px;
    }
    .offer {
        min-height: auto;
    }
    .offer__title {
        font-size: 25px;
        line-height: 1.2em;
        margin-bottom: 10px;
    }
    .offer__text {
        font-size: 16px;
    }
    .offer__discount {
        font-size: 90px;
    }
    .products__inner {
        padding-bottom: 0;
        .swiper-button-prev::after,
        .swiper-button-next::after {
            font-size: 30px;
        }
    }
    .review__name {
        font-size: 20px;
    }
    .review__text {
        font-size: 15px;
    }
    .blog__item {
        width: 80%;
    }
    .footer__inner {
        flex-direction: column;
    }
    .footer__item {
        margin-bottom: 20px;
    }
    .footer__social .social__item:last-child {
        margin-right: 0;
    }
}

@media only screen and (max-width:500px) {
    .header__inner {
        justify-content: space-around;
    }
    .header__social {
        display: none;
    }
    .header__btn {
        display: none;
    }
    .btn-second.nav__btn {
        display: block;
    }
    .social.nav__social{
        display: flex;
        margin-bottom: 15px;
    }
    .top {
        min-height: 485px;
    }
    .top-content {
        padding-top: 135px;
        margin-left: 20px;
    }
    .top-content__title {
        font-size: 40px;
    }
    .advantages-list__item {
        width: 80%;
    }
    .offers__item {
        padding-bottom: 20px;
        padding-top: 20px;
    }
    .offer {
        width: 70%;
        padding: 20px 10px;
    }
    .offer__title {
        font-size: 20px;
    }
    .offer__text {
        font-size: 14px;
    }
    .portfolio__inner {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 250px);
    }
    .portfolio__item--big {
        grid-column: span 1;
        grid-row: span 1;
    }
    .about__content-title {
        font-size: 20px;
    }
    .about__text {
        font-size: 16px;
    }
    .reviews-slider .reviews-slider__nav {
        display: none;
    }
    .blog__item {
        width: 100%;
    }
    .footer {
        padding-top: 20px;
    }
    .footer__phones {
        flex-direction: column;
    }
    .phones__item {
        margin-left: 0;
        margin-bottom: 15px;
    }
    .phones__namber {
        margin-left: 0;
    }
    .modal__content {
        width: 80%;
    }
}

@media only screen and (max-width:400px){
    .title {
        font-size: 25px;
        padding: 35px 0;
    }
    .advantages-list__item {
        width: 90%;
    }
    .service {
        width: 90%;
    }
    .offer {
      width: 80%;  
    }
    .portfolio__inner {  
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(6, 250px);
    }
    .products__inner {
        padding-bottom: 0;
    }
    .slider-products__nav {
        top: 15px;
    }
    .review__name {
        font-size: 20px;
    }
    .review__text {
        font-size: 14px;
    }
}