.products{
    padding-bottom: 60px;
}
.products__inner {
    position: relative;
    padding-bottom: 50px;
    .slider-products__pagination {
        width: 100%;
        bottom: 0;
        .swiper-pagination-bullet-active{
            background-color: $color-secondary;
        }
    }
    .swiper-button-prev{
        left: 0px;
    }
    .swiper-button-next {
        right: 0;
    }
    .swiper-button-prev::after,
    .swiper-button-next::after {
        color: $color-secondary;
    }
}
.slider-products{
    display: flex;
    align-items: center;
}

.product {
    max-width: 300px;
    height: 200px;
    display: flex;
    align-items: center;
}
.slider-products__nav {
    position: absolute;
    right: 0;
    top: 0;
    width: 100px;
    // height: 100px;
    background-color: currentColor;
}